import { Component, OnInit } from '@angular/core';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { AbsensiPilotRonService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router, ActivatedRoute } from '@angular/router';
import { Ron } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private fb: FormBuilderTypeSafe,
        private pesan: PesanService,
        private absensiPilotRonService: AbsensiPilotRonService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
    ) { }


    id_ron: string = ''
    ngOnInit() {
        this.id_ron = this.activatedRoute.snapshot.paramMap.get('id_ron')
        this.form();
        this.getData()
    }

    getData() {
        this.absensiPilotRonService.getAbsensiPilotRonsId(this.id_ron).subscribe(
            data=> {
                this.komponen.setValue(data.data)
            }
        )
    }
    komponen: FormGroupTypeSafe<Ron>;
    form() {
        this.komponen = this.fb.group<Ron>(
            {
                ron: new FormControl('', [Validators.required]),
                ron_code: new FormControl('', [Validators.required]),
                id_ron: new FormControl(''),
                default_nilai: new FormControl(''),
                created_at: new FormControl(''),
                updated_at: new FormControl(''),
            }
        )
    }

    getValueForm(fild, compare) {
        if (this.komponen.get(fild).value == compare) {
            return true;
        }
        return false;
    }

    simpan() {
        this.spinner.show()
        if (this.komponen.invalid) {
            console.log(this.komponen.invalid)
            this.pesan.pesanWarningForm('Form Tidak Valid');
            this.spinner.hide()
            return;
        }

        this.absensiPilotRonService.putAbsensiPilotRonsId({id: this.id_ron, body: this.komponen.value}).subscribe(
            data => {
                this.goToBack()
                this.spinner.hide()
            }, err => {
                this.pesan.pesanWarningForm('Gagal di simpan, mohon cek Kode Komponen');
                this.spinner.hide()
            },
            () => {
                console.log('HTTP request completed.')
            }
        )

    }

    goToBack() {
        this.router.navigate(['/hr/payroll/module/pengaturan/ron/list'])
    }

}
