import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

import { RonRoutingModule } from './ron-routing.module';
import { EditComponent } from './edit/edit.component';
import { ListComponent } from './list/list.component';
import { AddComponent } from './add/add.component';
import { NzSelectModule, NgZorroAntdModule } from 'ng-zorro-antd';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { FormBuilderTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { DetailComponent } from './detail/detail.component';

@NgModule({
    declarations: [EditComponent, ListComponent, AddComponent, DetailComponent],
    imports: [
        CommonModule,
        RonRoutingModule,
        NzSelectModule,
        NgZorroAntdModule,
        ReactiveFormsModule,
        FormsModule
    ],
    providers: [
        FormBuilderTypeSafe
    ]

})
export class RonModule { }
