import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { PayrollDerazonaRonGroupDetailService, PayrollDerazonaRonGroupService } from 'src/app/sdk/payroll/services';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'app-detail',
    templateUrl: './detail.component.html',
    styleUrls: ['./detail.component.scss']
})
export class DetailComponent implements OnInit {

    constructor(
        private spinner: NgxSpinnerService,
        private activeRouter: ActivatedRoute,
        private payrollDerazonaRonGroupDetailService: PayrollDerazonaRonGroupDetailService,
        private payrollDerazonaRonGroupService: PayrollDerazonaRonGroupService,
    ) { }

    id_ron = null;
    ngOnInit() {
        this.activeRouter.paramMap.subscribe(
            data => {
                if (data.get('id_ron')) {
                    this.id_ron = data.get('id_ron')
                    this.getData()
                }
            }
        )
    }

    listData = []
    getData() {
        this.payrollDerazonaRonGroupDetailService.getPayrollDerazonaRonGroupDetails(JSON.stringify({ id_ron: this.id_ron })).subscribe(
            data => {
                this.listData = data.data;

                this.listData.forEach((item, index) => {
                    this.listData[index]['edit'] = false;
                    this.getGroupName(item.id_ron_group, index)
                })

            }
        )
    }

    edit(id_pekerjaan) {
        this.listData.forEach((item, index) => {
            this.listData[index]['edit'] = false;
            if (id_pekerjaan == item.id_ron_group_detail) {
                this.listData[index]['edit'] = true;
            }
        })
    }

    SimpanUpdate(data, index) {

        delete data.edit;
        delete data.group;
        this.spinner.show()
        this.payrollDerazonaRonGroupDetailService.putPayrollDerazonaRonGroupDetailsId({ id: data.id_ron_group_detail, body: data }).subscribe(
            data => {
                this.spinner.hide()
                this.getData()
                // this.listData[index]['edit'] = false;

            },
            err => {
                this.spinner.hide()
            }
        )

    }

    getGroupName(id_group, index) {
        this.payrollDerazonaRonGroupService.getPayrollDerazonaRonGroupsId(id_group).subscribe(
            data => {
                this.listData[index]['group'] = data.data
            }
        )
    }

}
